import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import VModal from 'vue-js-modal'
import VCalendar from 'v-calendar';

Vue.use(VModal)
Vue.use(VCalendar, {
  componentPrefix: 'v'
});

Vue.prototype.$eventBus = new Vue();


Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
